          </div>

          <!-- æ•°æ®é¢„è§ˆ -->
          <div v-if="excelData.length > 0" class="preview-section">
            <h3>æ•°æ®é¢„è§ˆ (å‰5è¡Œ)</h3>
            <el-table :data="previewData" border style="width: 100%">
              <el-table-column
                v-for="(header, index) in excelHeaders"
