                  <li>ç‚¹å‡»VCFæ–‡ä»¶</li>
                  <li>é€‰æ‹©"é€šè®¯å½•"åº”ç”¨æ‰“å¼€</li>
                  <li>ç‚¹å‡»"æ·»åŠ æ‰€æœ‰è”ç³»äºº"</li>
                  <li>ç¡®è®¤å¯¼å…¥å®Œæˆ</li>
                </ol>
              </div>

              <div class="guide-section">
                <h4>æ³¨æ„äº‹é¡¹</h4>
                <ul>
                  <li><strong>å»ºè®®å…ˆä¸‹è½½æ¨¡æ¿</strong>ï¼ŒæŒ‰æ¨¡æ¿æ ¼å¼å¡«å†™æ•°æ®</li>
                  <li>Excelç¬¬ä¸€è¡Œå¿…é¡»ä¸ºå­—æ®µæ ‡é¢˜ï¼ˆå¦‚ï¼šå§“åã€ç”µè¯ç­‰ï¼‰</li>
                  <li>å§“åå’Œç”µè¯å­—æ®µä¸ºå¿…å¡«é¡¹ï¼Œä¸èƒ½ä¸ºç©º</li>
                  <li>ç”µè¯å·ç è‡ªåŠ¨æ ¼å¼åŒ–ï¼šä¸­å›½æ‰‹æœºå·æ·»åŠ +86å‰ç¼€</li>
                  <li>æ”¯æŒå¤å§“è¯†åˆ«ï¼šæ¬§é˜³ã€å¸é©¬ã€è¯¸è‘›ç­‰å¸¸è§å¤å§“</li>
                  <li>ç”ŸæˆvCard 3.0æ ¼å¼ï¼Œå…¼å®¹æ‰€æœ‰ä¸»æµè®¾å¤‡å’Œåº”ç”¨</li>
                  <li>å¦‚æœiOSæ— æ³•è¯†åˆ«ï¼Œè¯·å°è¯•é€šè¿‡é‚®ä»¶å‘é€VCFæ–‡ä»¶åˆ°è‡ªå·±é‚®ç®±ï¼Œç„¶ååœ¨é‚®ä»¶ä¸­æ‰“å¼€</li>
                </ul>
