<script>
import * as XLSX from 'xlsx'

export default {
  name: 'ExcelToVcfView',
  data() {
    return {
      metaInfo: {
        title: 'Excelè½¬VCFé€šè®¯å½• - æ‰¹é‡å¯¼å…¥è”ç³»äºº',
        meta: [
          { name: 'description', content: 'å°†Excelè¡¨æ ¼è½¬æ¢ä¸ºVCFé€šè®¯å½•æ–‡ä»¶ï¼Œæ”¯æŒæ‰¹é‡å¯¼å…¥è”ç³»äººåˆ°æ‰‹æœºã€Outlookç­‰è®¾å¤‡ã€‚' },
          { name: 'keywords', content: 'Excelè½¬VCF,é€šè®¯å½•è½¬æ¢,è”ç³»äººå¯¼å…¥,VCFæ–‡ä»¶ç”Ÿæˆ,æ‰¹é‡è”ç³»äºº' }
        ]
      },
      fileList: [],
      excelData: [],
      excelHeaders: [],
      fieldMapping: {
        name: null,
        phone: null,
        email: -1,
        company: -1,
        address: -1,
        note: -1
      },
      converting: false
    }
  },
  computed: {
    previewData() {
      return this.excelData.slice(0, 5).map(row => {
        const obj = {}
        row.forEach((cell, index) => {
          obj[`col${index}`] = cell
        })
        return obj
      })
    },
    canConvert() {
      return this.excelData.length > 0 && 
             this.fieldMapping.name !== null && 
             this.fieldMapping.phone !== null
    }
  },

  methods: {
    handleFileChange(file) {
      this.fileList = [file]
      this.readExcelFile(file.raw)
    },

    handleRemove() {
      this.fileList = []
      this.excelData = []
      this.excelHeaders = []
      this.resetFieldMapping()
    },

    resetFieldMapping() {
      this.fieldMapping = {
        name: null,
        phone: null,
        email: -1,
        company: -1,
        address: -1,
        note: -1
      }
    },

    async readExcelFile(file) {
      try {
        const data = await this.fileToArrayBuffer(file)
        const workbook = XLSX.read(data, { type: 'array' })
        const sheetName = workbook.SheetNames[0]
        const worksheet = workbook.Sheets[sheetName]
        
        // è½¬æ¢ä¸ºæ•°ç»„æ ¼å¼
        const jsonData = XLSX.utils.sheet_to_json(worksheet, { header: 1 })
        
        if (jsonData.length < 2) {
          this.$message.error('Excelæ–‡ä»¶è‡³å°‘éœ€è¦åŒ…å«æ ‡é¢˜è¡Œå’Œæ•°æ®è¡Œ')
          return
        }

        this.excelHeaders = jsonData[0]
        this.excelData = jsonData.slice(1).filter(row => row.some(cell => cell))
        
        // è‡ªåŠ¨æ˜ å°„å¸¸è§å­—æ®µ
        this.autoMapFields()
        
        this.$message.success(`æˆåŠŸè¯»å– ${this.excelData.length} æ¡æ•°æ®`)
      } catch (error) {
        console.error('è¯»å–Excelæ–‡ä»¶å¤±è´¥:', error)
        this.$message.error('è¯»å–Excelæ–‡ä»¶å¤±è´¥: ' + error.message)
      }
    },

    fileToArrayBuffer(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader()
        reader.onload = e => resolve(e.target.result)
        reader.onerror = reject
        reader.readAsArrayBuffer(file)
      })
    },

    autoMapFields() {
      // è‡ªåŠ¨è¯†åˆ«å¸¸è§çš„å­—æ®µå
      const nameKeywords = ['å§“å', 'åå­—', 'è”ç³»äºº', 'name', 'contact']
      const phoneKeywords = ['ç”µè¯', 'æ‰‹æœº', 'è”ç³»ç”µè¯', 'phone', 'mobile', 'tel']
      const emailKeywords = ['é‚®ç®±', 'é‚®ä»¶', 'ç”µå­é‚®ä»¶', 'email', 'mail']
      const companyKeywords = ['å…¬å¸', 'å•ä½', 'ç»„ç»‡', 'company', 'organization']
      const addressKeywords = ['åœ°å€', 'ä½å€', 'address', 'location']
      const noteKeywords = ['å¤‡æ³¨', 'è¯´æ˜', 'æè¿°', 'note', 'remark', 'description']

      this.excelHeaders.forEach((header, index) => {
        const lowerHeader = header.toLowerCase()
        
        if (nameKeywords.some(keyword => lowerHeader.includes(keyword.toLowerCase()))) {
          this.fieldMapping.name = index
        } else if (phoneKeywords.some(keyword => lowerHeader.includes(keyword.toLowerCase()))) {
          this.fieldMapping.phone = index
        } else if (emailKeywords.some(keyword => lowerHeader.includes(keyword.toLowerCase()))) {
          this.fieldMapping.email = index
        } else if (companyKeywords.some(keyword => lowerHeader.includes(keyword.toLowerCase()))) {
          this.fieldMapping.company = index
        } else if (addressKeywords.some(keyword => lowerHeader.includes(keyword.toLowerCase()))) {
          this.fieldMapping.address = index
        } else if (noteKeywords.some(keyword => lowerHeader.includes(keyword.toLowerCase()))) {
          this.fieldMapping.note = index
        }
      })
    },

    async convertToVcf() {
      if (!this.canConvert) {
        this.$message.warning('è¯·å…ˆé…ç½®å§“åå’Œç”µè¯å­—æ®µæ˜ å°„')
        return
      }

      this.converting = true

      try {
        let vcfContent = ''
        let successCount = 0
        let errorCount = 0

        for (let i = 0; i < this.excelData.length; i++) {
          const row = this.excelData[i]

          try {
            const contact = this.extractContactInfo(row)
            if (contact.name && contact.phone) {
              vcfContent += this.generateVcfEntry(contact)
              successCount++
            } else {
              errorCount++
            }
          } catch (error) {
            console.error(`å¤„ç†ç¬¬ ${i + 1} è¡Œæ•°æ®å¤±è´¥:`, error)
            errorCount++
          }
        }

        if (successCount === 0) {
          this.$message.error('æ²¡æœ‰æœ‰æ•ˆçš„è”ç³»äººæ•°æ®å¯è½¬æ¢')
          return
        }

        // ä¸‹è½½VCFæ–‡ä»¶
        this.downloadVcfFile(vcfContent)

        this.$message.success(`è½¬æ¢å®Œæˆï¼æˆåŠŸ: ${successCount} æ¡ï¼Œå¤±è´¥: ${errorCount} æ¡`)

      } catch (error) {
        console.error('è½¬æ¢å¤±è´¥:', error)
        this.$message.error('è½¬æ¢å¤±è´¥: ' + error.message)
      } finally {
        this.converting = false
      }
    },

    extractContactInfo(row) {
      const contact = {}

      // æå–å¿…å¡«å­—æ®µ
      contact.name = this.getFieldValue(row, this.fieldMapping.name)
      contact.phone = this.getFieldValue(row, this.fieldMapping.phone)

      // æå–å¯é€‰å­—æ®µ
      if (this.fieldMapping.email >= 0) {
        contact.email = this.getFieldValue(row, this.fieldMapping.email)
      }
      if (this.fieldMapping.company >= 0) {
        contact.company = this.getFieldValue(row, this.fieldMapping.company)
      }
      if (this.fieldMapping.address >= 0) {
        contact.address = this.getFieldValue(row, this.fieldMapping.address)
      }
      if (this.fieldMapping.note >= 0) {
        contact.note = this.getFieldValue(row, this.fieldMapping.note)
      }

      return contact
    },

    getFieldValue(row, fieldIndex) {
      if (fieldIndex < 0 || fieldIndex >= row.length) {
        return ''
      }
      const value = row[fieldIndex]
      return value ? String(value).trim() : ''
    },

    generateVcfEntry(contact) {
      let vcf = 'BEGIN:VCARD\r\n'
      vcf += 'VERSION:3.0\r\n'

      // å§“å (å¿…å¡«) - iOSå…¼å®¹æ ¼å¼
      if (contact.name) {
        vcf += `FN:${this.escapeVcfValue(contact.name)}\r\n`
        // ç®€åŒ–Nå­—æ®µæ ¼å¼ï¼ŒiOSæ›´å®¹æ˜“è¯†åˆ«
        const nameParts = this.parseChineseName(contact.name)
        vcf += `N:${this.escapeVcfValue(nameParts.lastName)};${this.escapeVcfValue(nameParts.firstName)};;;\r\n`
      }

      // ç”µè¯ (å¿…å¡«) - iOSæœ€å…¼å®¹çš„æ ¼å¼
      if (contact.phone) {
        const cleanPhone = this.cleanPhoneNumber(contact.phone)
        // iOSæœ€å–œæ¬¢çš„ç”µè¯æ ¼å¼
        vcf += `TEL;TYPE=CELL:${cleanPhone}\r\n`
      }

      // é‚®ç®± (å¯é€‰) - ç®€åŒ–æ ¼å¼
      if (contact.email) {
        vcf += `EMAIL:${this.escapeVcfValue(contact.email)}\r\n`
      }

      // å…¬å¸ (å¯é€‰)
      if (contact.company) {
        vcf += `ORG:${this.escapeVcfValue(contact.company)}\r\n`
      }

      // åœ°å€ (å¯é€‰) - ç®€åŒ–åœ°å€æ ¼å¼
      if (contact.address) {
        vcf += `ADR:;;${this.escapeVcfValue(contact.address)};;;;\r\n`
      }

      // å¤‡æ³¨ (å¯é€‰)
      if (contact.note) {
        vcf += `NOTE:${this.escapeVcfValue(contact.note)}\r\n`
      }

      vcf += 'END:VCARD\r\n'
      return vcf
    },

    // è§£æä¸­æ–‡å§“å
    parseChineseName(fullName) {
      const name = fullName.trim()
      if (name.length <= 1) {
        return { lastName: name, firstName: '' }
      } else if (name.length === 2) {
        return { lastName: name.charAt(0), firstName: name.charAt(1) }
      } else if (name.length === 3) {
        // å¸¸è§çš„ä¸‰å­—å§“åï¼šå§“1å­—ï¼Œå2å­—
        return { lastName: name.charAt(0), firstName: name.substring(1) }
      } else if (name.length === 4) {
        // å¯èƒ½æ˜¯å¤å§“æˆ–è€…å§“åéƒ½æ˜¯2å­—
        const commonCompoundSurnames = ['æ¬§é˜³', 'å¤ªå²', 'ç«¯æœ¨', 'ä¸Šå®˜', 'å¸é©¬', 'ä¸œæ–¹', 'ç‹¬å­¤', 'å—å®«', 'ä¸‡ä¿Ÿ', 'é—»äºº', 'å¤ä¾¯', 'è¯¸è‘›', 'å°‰è¿Ÿ', 'å…¬ç¾Š', 'èµ«è¿', 'æ¾¹å°', 'çš‡ç”«', 'å®—æ”¿', 'æ¿®é˜³', 'å…¬å†¶', 'å¤ªå”', 'ç”³å± ', 'å…¬å­™', 'æ…•å®¹', 'ä»²å­™', 'é’Ÿç¦»', 'é•¿å­™', 'å®‡æ–‡', 'å¸å¾’', 'é²œäº']
        const firstTwo = name.substring(0, 2)
        if (commonCompoundSurnames.includes(firstTwo)) {
          return { lastName: firstTwo, firstName: name.substring(2) }
        } else {
          return { lastName: name.charAt(0), firstName: name.substring(1) }
        }
      } else {
        // è¶…é•¿å§“åï¼Œå–ç¬¬ä¸€ä¸ªå­—ä½œä¸ºå§“
        return { lastName: name.charAt(0), firstName: name.substring(1) }
      }
    },

    escapeVcfValue(value) {
      if (!value) return ''
      return String(value)
        .replace(/\\/g, '\\\\')
        .replace(/;/g, '\\;')
        .replace(/,/g, '\\,')
        .replace(/\n/g, '\\n')
        .replace(/\r/g, '')
    },

    cleanPhoneNumber(phone) {
      if (!phone) return ''

      // è½¬æ¢ä¸ºå­—ç¬¦ä¸²å¹¶ç§»é™¤æ‰€æœ‰ç©ºæ ¼ã€æ¨ªçº¿ã€æ‹¬å·ç­‰æ ¼å¼å­—ç¬¦
      let cleaned = String(phone).replace(/[\s\-()ï¼ˆï¼‰]/g, '')

      // ç§»é™¤éæ•°å­—å’Œ+å·çš„å­—ç¬¦ï¼Œä½†ä¿ç•™æ•°å­—å’Œ+
      cleaned = cleaned.replace(/[^\d+]/g, '')

      // å¤„ç†ä¸­å›½æ‰‹æœºå·
      if (/^1[3-9]\d{9}$/.test(cleaned)) {
        // æ ‡å‡†çš„11ä½ä¸­å›½æ‰‹æœºå·ï¼Œæ·»åŠ +86
        return '+86' + cleaned
      }

      // å¤„ç†å·²æœ‰+86çš„ä¸­å›½æ‰‹æœºå·
      if (/^\+?86[1-9]\d{10}$/.test(cleaned)) {
        return cleaned.startsWith('+') ? cleaned : '+' + cleaned
      }

      // å¤„ç†å…¶ä»–å›½é™…å·ç 
      if (/^\+\d{7,15}$/.test(cleaned)) {
        return cleaned
      }

      // å¤„ç†å›ºå®šç”µè¯ç­‰å…¶ä»–æ ¼å¼
      if (/^\d{7,15}$/.test(cleaned)) {
        // å¦‚æœæ˜¯7-11ä½æ•°å­—ï¼Œå¯èƒ½æ˜¯å›ºå®šç”µè¯æˆ–å…¶ä»–å·ç 
        if (cleaned.length >= 10) {
          return '+86' + cleaned // å‡è®¾æ˜¯ä¸­å›½å·ç 
        }
        return cleaned
      }

      // å¦‚æœéƒ½ä¸åŒ¹é…ï¼Œè¿”å›æ¸…ç†åçš„åŸå§‹å·ç 
      return cleaned || phone
    },

    downloadVcfFile(vcfContent) {
      // iOSå…¼å®¹æ€§ä¼˜åŒ–ï¼šä¸ä½¿ç”¨BOMï¼Œç›´æ¥ä½¿ç”¨UTF-8
      const finalContent = vcfContent

      // ä½¿ç”¨iOSæœ€å…¼å®¹çš„MIMEç±»å‹
      const blob = new Blob([finalContent], {
        type: 'text/vcard;charset=utf-8'
      })

      const url = URL.createObjectURL(blob)
      const a = document.createElement('a')
      a.href = url

      // ä½¿ç”¨è‹±æ–‡æ–‡ä»¶åï¼Œé¿å…iOSä¸­æ–‡æ–‡ä»¶åé—®é¢˜
      const now = new Date()
      const dateStr = now.toISOString().slice(0, 10)
      const timeStr = now.toTimeString().slice(0, 5).replace(':', '')
      a.download = `contacts_${dateStr}_${timeStr}.vcf`

      // iOS Safariå…¼å®¹æ€§è®¾ç½®
      a.setAttribute('download', a.download)
      a.setAttribute('target', '_blank')
      a.style.display = 'none'

      document.body.appendChild(a)
      a.click()

      // å»¶è¿Ÿæ¸…ç†ï¼Œç¡®ä¿ä¸‹è½½å®Œæˆ
      setTimeout(() => {
        document.body.removeChild(a)
        URL.revokeObjectURL(url)
      }, 100)

      // ä¸ºiOSç”¨æˆ·æä¾›é¢å¤–æç¤º
      this.$message({
        message: 'VCFæ–‡ä»¶å·²ä¸‹è½½ï¼iOSç”¨æˆ·è¯·ï¼š1.æ‰“å¼€æ–‡ä»¶ 2.é€‰æ‹©"é€šè®¯å½•"åº”ç”¨ 3.ç‚¹å‡»"æ·»åŠ è”ç³»äºº"',
        type: 'success',
        duration: 8000,
        showClose: true
      })
    },

    clearAll() {
      this.fileList = []
      this.excelData = []
      this.excelHeaders = []
      this.resetFieldMapping()
      this.$message.info('å·²æ¸…ç©ºæ‰€æœ‰æ•°æ®')
    },

    downloadTemplate() {
      // åˆ›å»ºæ¨¡æ¿æ•°æ®
      let templateData = [
        // æ ‡é¢˜è¡Œ
        ['å§“å', 'ç”µè¯', 'é‚®ç®±', 'å…¬å¸', 'åœ°å€', 'å¤‡æ³¨'],
        //ç¤ºä¾‹æ•°æ®
        ['å¼ ä¸‰', '13800138000', 'zhangsan@example.com', 'é˜¿é‡Œå·´å·´', 'æ­å·å¸‚è¥¿æ¹–åŒº', 'æŠ€æœ¯æ€»ç›‘'],
        ['æå››', '13900139000', 'lisi@example.com', 'è…¾è®¯ç§‘æŠ€', 'æ·±åœ³å¸‚å—å±±åŒº', 'äº§å“ç»ç†'],
        ['ç‹äº”', '13700137000', 'wangwu@example.com', 'ç™¾åº¦å…¬å¸', 'åŒ—äº¬å¸‚æµ·æ·€åŒº', 'è®¾è®¡å¸ˆ'],
        ['èµµå…­', '13600136000', 'zhaoliu@example.com', 'å­—èŠ‚è·³åŠ¨', 'åŒ—äº¬å¸‚æœé˜³åŒº', 'è¿è¥ä¸“å‘˜'],
        ['é’±ä¸ƒ', '13500135000', 'qianqi@example.com', 'ç¾å›¢', 'åŒ—äº¬å¸‚æœé˜³åŒº', 'æ•°æ®åˆ†æå¸ˆ']
      ]
      try {
        // åˆ›å»ºå·¥ä½œç°¿
        const wb = XLSX.utils.book_new()

        // åˆ›å»ºå·¥ä½œè¡¨
        const ws = XLSX.utils.aoa_to_sheet(templateData)

        // è®¾ç½®åˆ—å®½
        const colWidths = [
          { wch: 10 }, // å§“å
          { wch: 15 }, // ç”µè¯
          { wch: 25 }, // é‚®ç®±
          { wch: 15 }, // å…¬å¸
          { wch: 20 }, // åœ°å€
          { wch: 15 }  // å¤‡æ³¨
        ]
        ws['!cols'] = colWidths

        // è®¾ç½®æ ‡é¢˜è¡Œæ ·å¼ï¼ˆå¦‚æœæ”¯æŒçš„è¯ï¼‰
        const range = XLSX.utils.decode_range(ws['!ref'])
        for (let col = range.s.c; col <= range.e.c; col++) {
          const cellAddress = XLSX.utils.encode_cell({ r: 0, c: col })
          if (!ws[cellAddress]) continue

          // è®¾ç½®å•å…ƒæ ¼æ ·å¼ï¼ˆåŸºç¡€ç‰ˆæœ¬å¯èƒ½ä¸æ”¯æŒï¼Œä½†ä¸ä¼šæŠ¥é”™ï¼‰
          ws[cellAddress].s = {
            font: { bold: true },
            fill: { fgColor: { rgb: "FFCCCCCC" } }
          }
        }

        // æ·»åŠ å·¥ä½œè¡¨åˆ°å·¥ä½œç°¿
        XLSX.utils.book_append_sheet(wb, ws, 'é€šè®¯å½•æ¨¡æ¿')

        // ç”Ÿæˆæ–‡ä»¶å
        const fileName = `é€šè®¯å½•æ¨¡æ¿_${new Date().toISOString().slice(0, 10)}.xlsx`

        // å¯¼å‡ºæ–‡ä»¶
        XLSX.writeFile(wb, fileName)

        this.$message.success('æ¨¡æ¿ä¸‹è½½æˆåŠŸï¼è¯·æŒ‰ç…§æ¨¡æ¿æ ¼å¼å¡«å†™è”ç³»äººä¿¡æ¯')

      } catch (error) {
        console.error('æ¨¡æ¿ä¸‹è½½å¤±è´¥:', error)
        this.$message.error('æ¨¡æ¿ä¸‹è½½å¤±è´¥: ' + error.message)
      }
    }
  }
}
